import React, { useState, TouchEvent, useRef, CSSProperties } from 'react';
import './index.scss';
import gameDragon from '../../img/gameDragon.svg';
import {useNavigate, useParams} from 'react-router-dom';
import textGameArray from '../utils/textGame';

interface MainGameplayProps {}

const MainGameplay: React.FC<MainGameplayProps> = () => {

    const [isDraggingUp, setIsDraggingUp] = useState<boolean>(false);
    const [touchStartY, setTouchStartY] = useState<number | null>(null);
    const [positionY, setPositionY] = useState<number>(0);

    const [positiveChoice, setPositiveChoice] = useState<boolean | null>(null)

    const containerRef = useRef<HTMLDivElement | null>(null);
    const { id } = useParams();
    const levelId = Number(id);
    const navigate = useNavigate()

    const handleTouchStart = (e: TouchEvent) => {
        const touch = e.touches[0];
        setTouchStartY(touch.clientY);
    };

    const handleTouchMove = (e: TouchEvent) => {
        if (touchStartY !== null) {
            const touch = e.touches[0];
            const currentY = touch.clientY;
            const deltaY = currentY - touchStartY;

            // Определяем направление перемещения
            setIsDraggingUp(deltaY < 0);

            // Обновляем позицию блока на основе расстояния перемещения

            const newPositionY = positionY + deltaY;
            if(positiveChoice === null) {
                if (newPositionY > 80) {
                    setPositionY(80);
                    setPositiveChoice(false)
                    setTimeout(() => {
                        navigate(`/dontAgree/${levelId}`)
                    }, 500)


                } else if (newPositionY < -80) {
                    setPositiveChoice(true)
                    setPositionY(-80);
                    setTimeout(() => {
                        navigate(`/agree/${levelId}`)
                    }, 500)

                } else {
                    setPositiveChoice(null)
                    setPositionY(newPositionY);
                }
            }

            // Запоминаем текущее положение Y для следующего события
            setTouchStartY(currentY);
        }
    };

    const handleTouchEnd = () => {
        setTouchStartY(null);
    };

    // Создаем стили для блока на основе позиции
    const blockStyle: CSSProperties = {
        transform: `translateY(${positionY}px)`,
    };

    return (
        <div
            className={'mainGameplay'}
            onTouchStart={handleTouchStart}
            onTouchMove={handleTouchMove}
            onTouchEnd={handleTouchEnd}
        >
            <div className={`itHappens ${positiveChoice ? 'itHappensUp' : ''}`}>Так бывает!</div>
            <div style={blockStyle} className={`${isDraggingUp ? 'text dragging-up' : 'text dragging-down'} bodyText ${positiveChoice !==null ? 'bodyTextUp' : ''}`} ref={containerRef}>
                    <span>{textGameArray[levelId - 1]}</span>
                    <img src={gameDragon} />
            </div>
            <div className={`itNotHappens ${positiveChoice === false ? 'itNotHappensUp' : ''}`}>Так не бывает!</div>
        </div>
    );
};

export default MainGameplay;
